<template>
  <h1>Справка по использованию</h1>
  <div v-html="content"></div>
</template>

<script>
import { useMeta } from "vue-meta"
import { config } from '@/plugins'
import { get, call } from 'vuex-pathify'

export default {
  name: "HelpView",
  data() {
    return {
    }
  },
  computed: {
    content: () => get('help@help').value,
  },
  mounted() {
    this.loadHelp()
  },
  methods: {
    ...call('help', [
      'loadHelp',
    ]),
  },
  setup() {
    useMeta({
      title: config.title("Справка"),
    })
  },
}
</script>